import { component, mixin, watch, createCell } from 'web-cell';
import { FormField } from 'boot-cell/source/Form/FormField';
import { Button } from 'boot-cell/source/Form/Button';

import { mergeList } from '../../utility';
import { RouteRoot } from '../menu';
import CommonSupplies from './Supplies';
import {
    SuppliesRequirement,
    GeoCoord,
    Supplies,
    Contact,
    searchAddress,
    suppliesRequirement,
    history
} from '../../model';
import { SessionBox } from '../../component';

type HospitalEditProps = SuppliesRequirement & { loading?: boolean };

@component({
    tagName: 'hospital-edit',
    renderTarget: 'children'
})
export class HospitalEdit extends mixin<{ srid: string }, HospitalEditProps>() {
    @watch
    srid = '';

    state = {
        loading: false,
        hospital: '',
        province: '',
        city: '',
        district: '',
        address: '',
        coords: {} as GeoCoord,
        url: '',
        supplies: CommonSupplies as Supplies[],
        contacts: [{} as Contact],
        remark: ''
    };

    async connectedCallback() {
        super.connectedCallback();

        if (!this.srid) return;

        await this.setState({ loading: true });

        const {
            hospital,
            province,
            city,
            district,
            address,
            coords,
            url,
            supplies,
            contacts,
            remark
        } = await suppliesRequirement.getOne(this.srid);

        this.setState({
            loading: false,
            hospital,
            province,
            city,
            district,
            address,
            coords,
            url,
            supplies: mergeList<Supplies>(
                'name',
                this.state.supplies,
                supplies
            ),
            contacts,
            remark
        });
    }

    changeText = ({ target }: Event) => {
        const { name, value } = target as HTMLInputElement;

        this.state[name] = value;
    };

    searchAddress = async ({ target }: Event) => {
        const { value } = target as HTMLInputElement;

        await this.setState({ loading: true });

        try {
            const [
                { pname, cityname, adname, address, location }
            ] = await searchAddress(value);

            const [longitude, latitude] = location.split(',').map(Number);

            await this.setState({
                loading: false,
                province: pname,
                city: cityname,
                district: adname,
                address,
                coords: { latitude, longitude }
            });
        } catch {
            await this.setState({ loading: false });
        }
    };

    changeListItem(
        key: keyof SuppliesRequirement,
        index: number,
        event: Event
    ) {
        event.stopPropagation();

        const { name, value } = event.target as HTMLInputElement;

        this.state[key][index][name] = value;
    }

    addSupply = () => this.setState({ supplies: [...this.state.supplies, {}] });

    deleteListItem(key: keyof SuppliesRequirement, index: number) {
        const list = this.state[key];

        list.splice(index, 1);

        this.setState({ [key]: list });
    }

    addContact = () =>
        this.setState({
            contacts: [...this.state.contacts, { name: '', number: '' }]
        });

    handleSubmit = async (event: Event) => {
        event.preventDefault();

        await this.setState({ loading: true });

        const { loading, supplies, ...data } = { ...this.state };

        try {
            await suppliesRequirement.update(
                { ...data, supplies: supplies.filter(({ count }) => count) },
                this.srid
            );
            self.alert('');

            history.push(RouteRoot.Hospital);
        } finally {
            await this.setState({ loading: false });
        }
    };

    render(
        _,
        {
            hospital,
            province,
            city,
            district,
            address,
            url,
            supplies,
            contacts,
            remark,
            loading
        }: HospitalEditProps
    ) {
        return (
            <SessionBox>
                <h2></h2>

                <form onChange={this.changeText} onSubmit={this.handleSubmit}>
                    <FormField
                        name="hospital"
                        required
                        defaultValue={hospital}
                        label=""
                        placeholder=""
                        onChange={this.searchAddress}
                    />

                    <FormField label="">
                        <div className="input-group">
                            <input
                                type="text"
                                className="form-control"
                                name="province"
                                required
                                defaultValue={province}
                                placeholder="///"
                            />
                            <input
                                type="text"
                                className="form-control"
                                name="city"
                                required
                                defaultValue={city}
                                placeholder="/"
                            />
                            <input
                                type="text"
                                className="form-control"
                                name="district"
                                required
                                defaultValue={district}
                                placeholder="//"
                            />
                            <input
                                type="text"
                                className="form-control"
                                name="address"
                                required
                                defaultValue={address}
                                placeholder=""
                            />
                        </div>
                    </FormField>

                    <FormField
                        type="url"
                        name="url"
                        required
                        defaultValue={url}
                        label=""
                    />
                    <FormField label="">
                        {supplies.map(({ name, count, remark }, index) => (
                            <div
                                className="input-group my-1"
                                onChange={(event: Event) =>
                                    this.changeListItem(
                                        'supplies',
                                        index,
                                        event
                                    )
                                }
                            >
                                <input
                                    type="text"
                                    className="form-control"
                                    name="name"
                                    value={name}
                                    placeholder=""
                                />
                                <input
                                    type="number"
                                    className="form-control"
                                    name="count"
                                    defaultValue="0"
                                    value={count}
                                    placeholder=""
                                />
                                <input
                                    type="text"
                                    className="form-control"
                                    name="remark"
                                    value={remark}
                                    placeholder=""
                                />
                                <div className="input-group-append">
                                    <Button onClick={this.addSupply}>+</Button>
                                    <Button
                                        kind="danger"
                                        disabled={supplies.length < 2}
                                        onClick={() =>
                                            this.deleteListItem(
                                                'supplies',
                                                index
                                            )
                                        }
                                    >
                                        -
                                    </Button>
                                </div>
                            </div>
                        ))}
                    </FormField>

                    <FormField label="">
                        {contacts.map(({ name, number }, index) => (
                            <div
                                className="input-group my-1"
                                onChange={(event: Event) =>
                                    this.changeListItem(
                                        'contacts',
                                        index,
                                        event
                                    )
                                }
                            >
                                <input
                                    type="text"
                                    className="form-control"
                                    name="name"
                                    value={name}
                                    placeholder=""
                                />
                                <input
                                    type="tel"
                                    className="form-control"
                                    name="number"
                                    value={number}
                                    placeholder=" +86  0"
                                />
                                <div className="input-group-append">
                                    <Button onClick={this.addContact}>+</Button>
                                    <Button
                                        kind="danger"
                                        disabled={!contacts[1]}
                                        onClick={() =>
                                            this.deleteListItem(
                                                'contacts',
                                                index
                                            )
                                        }
                                    >
                                        -
                                    </Button>
                                </div>
                            </div>
                        ))}
                    </FormField>

                    <FormField
                        is="textarea"
                        name="remark"
                        label=""
                        defaultValue={remark}
                    />
                    <div className="form-group mt-3">
                        <Button type="submit" block disabled={loading}>
                            
                        </Button>
                        <Button
                            type="reset"
                            kind="danger"
                            block
                            onClick={() => history.push(RouteRoot.Hospital)}
                        >
                            
                        </Button>
                    </div>
                </form>
            </SessionBox>
        );
    }
}
